import DataTableZ from "../components/dataTable";
import "../../../../common/extensions/global";
import Global from "../components/Global";
import { DefaultStatus, getStatusText } from "../common/DefaultStatus";
import DtPicker from "../components/dateTimePickerHelper";
import "../../scss/pages/essayType-manage.scss";
import "../common/extensions/jquery-et";
import "bootstrap";

(() => {

    let taxonomyType = "EssayType"
    let tableApi = `/api/${taxonomyType}/Table`;
    let taxonomyEdit = `/api/${taxonomyType}/Edit`;
    let softDeleteApi = `/api/${taxonomyType}/Delete`;
    let listApi = `/api/${taxonomyType}/List`;
    let changeStatus = `/api/${taxonomyType}/ChangeStatus`;
    $.get(listApi,
        (msg: any) => {
            if (msg.Code === 0) {
                let data = msg.Data;
                let optionsHtml =
                    `<option value="0|-1">无父级</option>` +
                    data.map((item: any) => {
                        return `<option value="${item.Id}|${item.Level}">${item.Name}</option>`
                    }).join(" ");
                $("#parentId").html(optionsHtml).selectpicker('refresh');
            } else {
                Global.alertError(msg.Error);
            }

        });

    const eventManager: any = (action: string, idArr: Array<string | number> = null) => {
        if (!idArr) {
            idArr = dt.selectIdArr;
        }
        if (idArr.length === 0) {
            return;
        }
        switch (action.toLocaleLowerCase()) {
            case "del":
                Global.showLoading();
                let result = DataTableZ.defautlDelete
                    (dt, softDeleteApi,
                        idArr,
                        (msg: any) => {
                            dt.reload();
                            Global.hideLoading();
                            dt.reload();
                        },
                        (jqXHR: any, textStatus: string, errorThrown: string) => {
                            console.log(jqXHR, textStatus, errorThrown);
                            alert("网络错误");
                            Global.hideLoading();
                        });
                if (!result) {
                    Global.hideLoading();
                }
                break;
            case "draft":
                $.post(changeStatus, {
                    idArr: idArr,
                    status: DefaultStatus.Draft
                }, (msg: any) => {
                    dt.reload();
                })
                break;
            case "published":
                $.post(changeStatus, {
                    idArr: idArr,
                    status: DefaultStatus.Published
                }, (msg: any) => {
                    dt.reload();
                })
                break;
            case "secret":
                $.post(changeStatus, {
                    idArr: idArr,
                    status: DefaultStatus.Secret
                }, (msg: any) => {
                    dt.reload();
                })
                break;
            default:
                break;
        }
    }

    let createDtPicker: DtPicker;
    let modifyDtPicker: DtPicker;

    /**
     * 初始化DtPicker
     */
    const initDtPicker = () => {
        createDtPicker = new DtPicker("#createDateTimePicker", {
            widgetPositioning: {
                horizontal: 'auto',
                vertical: 'top'
            }
        });
        modifyDtPicker = new DtPicker("#modifyDateTimePicker", {
            widgetPositioning: {
                horizontal: 'auto',
                vertical: 'top'
            }
        });

        createDtPicker.on("dp.change", (e: any) => {
            if (e.date) {
                let newDate = createDtPicker.getDt();
                if (newDate) {
                    modifyDtPicker.setMinDt(createDtPicker.getDt());
                }
            }
        });

        modifyDtPicker.on("dp.change", (e: any) => {
            if (e.date) {
                let newDate = modifyDtPicker.getDt();
                if (newDate) {
                    createDtPicker.setMaxDt(newDate);
                }
            }
        });

        $("#curTime").click(() => {
            modifyDtPicker.setDt(new Date());
        });
    }

    initDtPicker();

    let firstFlag = true;
    const initSelector = () => {
        if (firstFlag) {
            $('#parentId').parent().children("button").click();
            $('#status').parent().children("button").click();
            firstFlag = false;
        }
    }

    const resetEdit = () => {
        $('#taxonomyName').val("");
        $('#parentId').val("").selectpicker('refresh');
        createDtPicker.clear();
        modifyDtPicker.clear();
        $('#status').val("").selectpicker('refresh');
    }

    const bindEvent = () => {
        $(".create").click(() => {
            resetEdit();
            $('#editModal').modal('show');
            initSelector();
        })

        $(".table-tool .filter").click((e: any) => {
            DataTableZ.defaultFilterEvent(dt);
        });

        $(".table-tool .reset").click((e: any) => {
            dt.resetFilter();
        });

        $(".del").click(() => {
            eventManager("del");
        });

    }

    /**每次重绘后为状态按钮添加事件 */
    const bindDrawCallBackEvent = () => {
        $("tbody .row-del").on("click",
            (e: any) => {
                e.preventDefault();
                let id = $(e.target).data("id");
                eventManager("del", [id]);
            });

        $("tbody .status").on("click", (e: any) => {
            let $this = $(e.target);
            let curStatus = parseInt($this.data("status"));
            let id = $this.data("id");
            switch (curStatus) {
                case DefaultStatus.Deleted:
                    eventManager('Draft', [id]);
                    break;
                case DefaultStatus.Draft:
                    eventManager('Published', [id]);
                    break;
                case DefaultStatus.Published:
                    eventManager('Secret', [id]);
                    break;
                case DefaultStatus.Secret:
                    eventManager('Published', [id]);
                    return;
            }
        });
    }
    let dt = new DataTableZ();
    let dtZConfig = {
        selector: "#dataTable",
        columnDefs: [
            {
                targets: 3,
                visible: true
            }
        ],
        "order": [[3, "desc"]],
        columns: [
            DataTableZ.checkBoxCol(),//0
            {
                "data": "Name", "title": "名称",
                render: (data: any, type: any, row: any, meta: any) => {
                    let color = "badge-info";
                    let status = row["Status"];

                    if (status === DefaultStatus.Published) {
                        color = "badge-success";
                    } else if (status === DefaultStatus.Secret) {
                        color = "badge-warning";
                    } else if (status === DefaultStatus.Deleted) {
                        color = "badge-danger";
                    }

                    let html = `${data} <span class="badge ${color} status" data-status="${status}" data-id="${row["Id"]}">${getStatusText(status)}</span>`
                    return html;
                },
            },
            {
                "data": "ParentName", "title": "父级系列",
            },
            {
                "data": "Create", "title": "创建时间",
                render: (data: any, type: any, row: any, meta: any) => {
                    return data.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss");
                },
            },
            {
                "data": "Modify", "title": "修改时间",
                render: (data: any, type: any, row: any, meta: any) => {
                    return data.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss");
                },
            },
            {
                "data": "Num", "title": "文章数"
            },
            {
                "data": "Level", "title": "层级"
            },
            {
                "data": "Id", "title": "操作",
                render: (data: any, type: any, row: any, meta: any) => {
                    return `<div class="btn-group ">                    
                    <button class="btn btn-secondary btn-sm text-white row-edit">编辑</button>
                    <button class="btn btn-danger btn-sm row-del" data-id="${data}">删除</button>                    
                </div>`;
                }
            }
        ],
        ajaxUrl: tableApi,
        initComplete: () => {
            bindEvent();
        },
        afterDraw: (setting: any, data: Array<any>) => {
            bindDrawCallBackEvent();

            //编辑按钮被点击
            $(".row-edit").click((e: any) => {
                var $tr = $(e.target).parents("tr");
                let id = parseInt(<string>$tr.find(".row-selector>input").val());

                var row = data.filter((item: any) => {
                    return item.Id === id;
                })[0];
                let parentId = row["ParentId"] + "|" + (row["Level"] - 1);
                console.log(parentId, row);
                $('#taxonomyName').val(row["Name"]);
                $('#parentId').val(parentId).selectpicker('render');
                createDtPicker.setDt(row["Create"]);
                // modifyDtPicker.setDt(row["Modify"]);
                $('#status').val(row["Status"]).selectpicker('render');

                $('#editModal').data("id", id);
                $('#editModal').modal('show');
                initSelector();
            });
        }
    };

    dt.render(dtZConfig);


    /**
     * 确认回复
     */
    $("#submitEdit").click(function () {
        var content = $("#taxonomyName").val();
        if (!content) {
            alert("分类名称不能为空！");
            return;
        }

        let pid_level = (<string>$('#parentId').val()).split("|")
            .map((item: any) => {
                return parseInt(item);
            })

        let obj: any = {};
        obj.name = $('#taxonomyName').val();
        obj.parentId = pid_level[0];
        obj.level = pid_level[1] + 1;
        obj.create = $('#create').val();
        obj.modify = $('#modify').val();
        obj.status = $("#status").val();
        let id: any = $("#editModal").data("id");
        if (id) {
            obj.id = id;
        } else {
            obj.id = 0;
        }

        Global.showLoading();
        $.ajax({
            url: taxonomyEdit,
            data: obj,
            type: "POST",
            dataType: "JSON",
            async: true,
            success(msg) {
                if (msg.Code === 0) {
                    Global.alert("操作成功");
                    dt.reload();
                } else {
                    Global.alertError(msg.Error);
                }
            },
            error() {
                alert("网络错误！");
            },
            complete() {
                Global.hideLoading();
            }
        });

        $('#editModal').modal('hide');
    });
})();