import "../common/extensions/jquery-et";

class DtPicker {

    /**
     *
     */
    constructor(selector: string,config:any = null) {
        this.$selector = $(selector);
        this.init(selector, config);
    }
    $selector: any = null;

    private defaultConfig:any={
        format: 'YYYY-MM-DD HH:mm:ss Z',
        locale: 'zh',
        widgetPositioning: {
            horizontal: 'auto',
            vertical: ''
        },
        // Icons
        // as Bootstrap 4 is not using Glyphicons anymore
        icons: {
            time: 'fa fa-clock-o',
            date: 'fa fa-calendar',
            up: 'fa fa-chevron-up',
            down: 'fa fa-chevron-down',
            previous: 'fa fa-chevron-left',
            next: 'fa fa-chevron-right',
            today: 'fa fa-check',
            clear: 'fa fa-trash',
            close: 'fa fa-times'
        }
    };

    init(selector: string, config:any = null) {
        if(config){
            Object.assign(this.defaultConfig,config);
        };
        this.$selector.datetimepicker(this.defaultConfig);
    }

    setDt(date: number | string | Date) {
        if (date instanceof Date) {
            this.$selector.data("DateTimePicker")
                .date(date);
        } else {
            this.$selector.data("DateTimePicker")
                .date(new Date(date));
        }
        
    }

    setMinDt(date: number | string | Date) {
        if (date instanceof Date) {
            this.$selector.data("DateTimePicker")
                .minDate(date);
        } else {
            this.$selector.data("DateTimePicker")
                .minDate(new Date(date));
        }
    }

    setMaxDt(date: number | string | Date) {
        if (date instanceof Date) {
            this.$selector.data("DateTimePicker")
                .maxDate(date);
        } else {
            this.$selector.data("DateTimePicker")
                .maxDate(new Date(date));
        }
    }

    getDt(returnDate: boolean = false) {
        let dateStr = <string>$('#createDateTimePicker').children("input").val();
        if (returnDate) {
            return new Date(dateStr);
        }
        return dateStr;
    }

    getTimestamp() {
        let date = <Date>this.getDt(true);
        return date.getTime();
    }

    setTimestamp(timestamp: number) {
        this.setDt(timestamp);
    }

    on(event:string,callback:any){
        this.$selector.on(event,function (e:any) {
            typeof callback === "function" && callback(e);
        });
    }

    clear(){
        this.$selector.data("DateTimePicker")
            .clear();
    }
}


export default DtPicker;
